<template>
  <div class="MaterialAdd">
    <v-dialog v-model="dialog" width="auto">
      <template v-slot:activator="{ on }">
        <v-icon color="red lighten-2" dark v-on="on" @click="addProcess"
          >mdi-plus-circle</v-icon
        >
      </template>

      <v-card :color="$store.state.colorSet.bgColor2">
        <v-card-title :class="titleStyle" primary-title>{{
          title
        }}</v-card-title>

        <v-card-text>
          <v-form ref="form">
            <v-row>
              <v-col cols="12" md="4">
                <v-text-field
                  type="date"
                  label="領貨日期"
                  v-model="list.date"
                  :rules="emptyRules"
                ></v-text-field>
              </v-col>
              <!-- <v-col cols="12" md="4">
                <v-combobox
                  label="店名"
                  :items="JSON.parse(this.$store.state.cData.branch)"
                  v-model="list.branch"
                  prepend-icon="mdi-store"
                  :rules="emptyRules"
                  autofocus
                  v-if="dialog"
                >
                </v-combobox>
              </v-col> -->
               <v-col cols="12" md="4">
                <v-autocomplete
                  :items="presonnelItems"
                  prepend-icon="mdi-human-greeting"
                  label="領用人"
                  v-model="list.takeman"
                  :rules="emptyRules"
                  return-object
                >
                </v-autocomplete>
              </v-col>
              <v-col cols="12">
                <v-autocomplete
                  label="材料 - 規格"
                  :items="materialItems"
                  v-model="list.material"
                  :rules="emptyRules"
                  ref="materialname"
                  return-object
                >
                </v-autocomplete>
              </v-col>

              <v-col cols="12" md="4">
                <v-text-field
                  type="number"
                  label="數量"
                  v-model="list.quantity"
                  :rules="emptyRules"
                ></v-text-field>
              </v-col>
              <v-col cols="12">
                <v-text-field label="備註" v-model="list.note"></v-text-field>
              </v-col>
            </v-row>
          </v-form>

        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn class="primary" @click="addOK" v-if="processType == 'add'"
            >新增到領取資料列表</v-btn
          >
          <v-btn class="success" @click="editOK" v-if="processType == 'edit'"
            >確認修改</v-btn
          >
        </v-card-actions>

        <v-card class="mx-2 grey lighten-4" v-if="tempDatas.length">
          <v-card-title>
            預計領取資料列表
            <v-spacer></v-spacer>
            總數量: {{ quantityTotal }}
          </v-card-title>
          <v-card-text>
            <v-simple-table fixed-header class="text-no-wrap" max-height="65vh">
              <template v-slot:default>
                <thead>
                  <tr class="green  lighten-1 white--text font-weight-bold">
                    <td></td>
                    <td>領取日期</td>
                    <td>領用人</td>
                    <td>品名</td>
                    <td>數量</td>
                    <td>備註</td>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="(item,index) of tempDatas" :key="index">
                    <td>
                     <v-icon class="error" @click.stop="delTempData(index)">mdi-close</v-icon>
                     {{index + 1}}.
                    </td>
                    <td> {{ item.date }}</td>
                    <td> {{ item.takeman.text }} </td>
                    <td> {{ item.material.text }} </td>
                    <td> {{ item.quantity }} </td>
                    <td> {{ item.note }} </td>
                  </tr>
                </tbody>
              </template>
            </v-simple-table>
          </v-card-text>

          <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn class="error" @click="finishOK">完成領取</v-btn>
        </v-card-actions>
        </v-card>

        <!-- <pre>{{ list }}</pre> -->
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import dayjs from "dayjs";

export default {
  data() {
    return {
      dialog: false,
      list: {}, //表單內資料
      oldList: {}, //未修改前的表單內資料
      processType: "", //存放頁面執行是add新增或edit修改
      title: "",
      titleStyle: "",
      emptyRules: [(v) => !!v || "不可空白"],
      tempDatas:[], //暫存入庫前資料
      presonnelItems:[],  //暫存店內人員
    };
  },

  computed: {
    materialItems() {
      return this.$store.state.materialItems.map((i) => {
        return {
          text: `${i.name}`,
          name: i.name,
          snkey: i.snkey,
        };
      });
    },

    quantityTotal(){
      return this.tempDatas.reduce((total,item)=>{
        return total += parseInt(item.quantity)
      },0);
    }
  },

  mounted() {
    //父組件要執行本組件時的function時的設定 (showFromParent) id是暫存傳過來的值
    this.$on("fromParent", function (item) {
      this.dialog = true;
      this.editProcess(item);
    });
    this.getPersonnel();
  },

  methods: {
    addProcess() {
      //進入畫面時,執行的是新增作業
      this.processType = "add";
      this.title = "新增入庫資料";
      this.titleStyle = "font-weight-black error lighten-2";
      this.list = {
        date: dayjs().format("YYYY-MM-DD"),
        price :0,
        quantity:0
      };
      this.$nextTick(() => {
        this.$refs.form.resetValidation();
      });
    },
    //進入畫面時,執行的是修改作業
    editProcess(item) {
      this.processType = "edit";
      this.title = "修改入庫資料";
      this.titleStyle = "font-weight-black success lighten-2";
      this.list = JSON.parse(JSON.stringify(item)); //複制一份實際修改資料內容
      this.oldList = JSON.parse(JSON.stringify(item));
      this.tempDatas = [];
    },
    //確認新增 -> 入庫列表
    addOK() {
      if (this.$refs.form.validate()) {
        this.list.personnel_snkey = this.$store.state.pData.snkey;
        this.list.create_man = this.$store.state.pData.name + "(" + dayjs().format("YYYY-MM-DD HH:mm:ss") + ")"; //執行動作的人,時間,內容

        this.tempDatas.push({...this.list})
        this.list.price = 0;
        this.list.quantity = 1;
        this.$refs.materialname.focus();
        this.$refs.form.resetValidation();
      }
    },
    delTempData(index){
      this.tempDatas.splice(index,1);
    },
    //完成領取作業
    finishOK() {
      this.dialog = false;

      var runRS = new Promise((resolve, reject) => {
        this.tempDatas
          .forEach((tempItem) => {
            let postData = {
              personnel_snkey: tempItem.personnel_snkey,
              datalist: JSON.stringify(tempItem),
            };

            this.myAxios(
              "general/add/" +
                this.$store.state.databaseName +
                "/materialexport",
              postData
            )
              .then((rs) => {
                // if (rs.state == 1) {
                  // this.$swal({ icon: "success", text: "已新增" });
                  // this.$emit("getAllData");
                  resolve(rs);
                // }
              })
              .catch((err) => {
                this.$swal({ icon: "err", text: "資料存取錯誤!!內容:" + err });
                reject(err);
              });

            //更新庫存
            let matchData = this.$store.state.materialItems.find((i) => {
              return i.snkey == tempItem.material.snkey;
            });
            if (!matchData.store){matchData.store = 0}
            matchData.store -= parseInt(tempItem.quantity);
            this.updateStore("material", matchData); //自制更新庫存的全局函數('資料庫名',內容)
            //更新庫存end
          });
      });

      //做完上面的才執行這裡
      runRS.then(() => {
        this.$swal({ icon: "success", text: "已新增" });
        this.$emit("getAllData");
        this.tempDatas = [];
      });

    },  
    //確認修改
    editOK() {
      if (this.$refs.form.validate()) {
        if (this.list.edit_man == undefined) {
          this.list.edit_man = "";
        }
        this.list.edit_man = this.$store.state.pData.name + "(" + dayjs().format("YYYY-MM-DD HH:mm:ss") + ")" + this.list.edit_man; //執行動作的人,時間,內容
        let postData = {
          snkey: this.list.snkey,
          datalist: JSON.stringify(this.list),
        };

        this.dialog = false;
        
        this.myAxios("general/edit/" + this.$store.state.databaseName + "/materialexport", postData)
          .then((rs) => {
            if (rs.state == 1) {
              this.$swal({ icon: "success", text: "已修改" });
              this.$emit('editFinish',this.list)
            }
          })
          .catch((err) => {
            this.$swal({ icon: "err", text: "資料存取錯誤!!內容:" + err });
          });

        //更新庫存
        let matchData = this.$store.state.materialItems.find((i) => {
          return i.snkey == this.list.material.snkey;
        });
        matchData.store = parseInt(matchData.store) + parseInt(this.oldList.quantity) - parseInt(this.list.quantity);
        this.updateStore('material', matchData);  //自制更新庫存的全局函數('資料庫名',內容)
        //更新庫存end  
      }
    },
    //取得所有人員資料
    getPersonnel() {
      this.myAxios("general/getAll/" + this.$store.state.databaseName + "/personnel").then((rs) => {
        this.presonnelItems = rs.map((i) => {
          let newItem = JSON.parse(i.datalist);
          return {
            snkey : newItem.snkey,
            text : newItem.name,
          };
        });
      });
    },
  },
};
</script>