<template>
  <div class="MaterialImportList">
    <v-card :color="$store.state.colorSet.bgColor2">
      <v-card-title>
        <!-- 入庫管理 -->
        <!-- <v-img src="@/assets/img/souko_shiwake.png" aspect-ratio="2.5" contain></v-img> -->

        <v-toolbar dense rounded>
          <v-text-field
            hide-details
            prepend-icon="mdi-magnify"
            single-line
            label="搜尋"
            v-model="searchKey"
            v-if="auth.material_search_key"
          ></v-text-field>

          <popupadd
            ref="editProcess"
            @getAllData="getAllData"
            @editFinish="editFinish"
            v-if="auth.material_add_key"
          ></popupadd>
        </v-toolbar>
      </v-card-title>
      <v-card-text>
        <v-data-iterator
          :items="searchfilter"
          :items-per-page.sync="itemsPerPage"
          :footer-props="{ itemsPerPageOptions }"
        >
          <template v-slot:default="props">
            <v-simple-table fixed-header class="text-no-wrap">
              <template v-slot:default>
                <thead class="title">
                  <tr>
                    <th class="text-left"></th>
                    <th class="text-left">日期</th>
                    <th class="text-left">協力廠商</th>
                    <th class="text-left">收貨人</th>
                    <th class="text-left">材料- 規格</th>
                    <th class="text-left">價格</th>
                    <th class="text-left">數量</th>
                    <th class="text-left">備註</th>
                    <th class="text-left">創建人員</th>
                    <th class="text-left">修改紀錄</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="item in props.items" :key="item.snkey">
                    <td>
                      <v-menu transition="scale-transition" offset-y>
                        <template v-slot:activator="{ on }">
                          <v-btn icon v-on="on">
                            <img src="@/assets/gear.svg" alt="">
                          </v-btn> 
                        </template>
                        <v-list>
                          <v-list-item
                            @click="edit(item)"
                            v-if="auth.material_edit_key"
                          >
                            <v-list-item-title>修改</v-list-item-title>
                          </v-list-item>
                          <v-list-item
                            @click="del(item)"
                            v-if="auth.material_del_key"
                          >
                            <v-list-item-title>刪除</v-list-item-title>
                          </v-list-item>
                        </v-list>
                      </v-menu>
                    </td>
                    <td>{{ item.date }}</td>
                    <td><span v-if="item.thirdparty"> {{ item.thirdparty.text }} </span> </td>
                    <td><span v-if="item.takeman"> {{ item.takeman.text }} </span></td>
                    <td>{{ item.material.text }}</td>
                    <td>{{ item.price }}</td>
                    <td>{{ item.quantity }}</td>
                    <td>{{ item.note }}</td>
                    <td>{{ item.create_man }}</td>
                    <td>{{ item.edit_man }}</td>
                  </tr>
                </tbody>
              </template>
            </v-simple-table>
          </template>
        </v-data-iterator>
      </v-card-text>
      <!-- <pre>{{ items }}</pre> -->
    </v-card>
  </div>
</template>

<script>
import popupadd from "@/views/main/material/MaterialImportAdd";
import dayjs from "dayjs";

export default {
  components: { popupadd },
  data() {
    return {
      items: [], //目前讀取進來的資料庫
      searchKey: "",
      auth: "", //各畫面授權使用功能限定(載入放在mounted)
      itemsPerPageOptions: [10, 20, 30],
      itemsPerPage: 10,

    };
  },

  mounted() {
    this.auth = this.$store.state.pData;
    this.getAllData();
  },

  computed: {
    searchfilter() {
      var keys = this.searchKey.split(" ");
      var str = "";

      return keys.reduce(function (prev, element) {
        return prev.filter((item) => {
          str = JSON.stringify(item).toUpperCase();
          if (str.includes(element.toUpperCase())) {
            return item;
          }
        });
      }, this.items);
    },
  },

  methods: {
    //取得所有資料
    getAllData() {
      let url =
        "general/getAll/" + this.$store.state.databaseName + "/materialimport";

      this.myAxios(url).then((rs) => {
        let newrs = rs.map((i) => {
          let newItem = JSON.parse(i.datalist);
          newItem.snkey = i.snkey;
          return newItem;
        });
        this.items = newrs.sort((a, b) => {
          return a.date < b.date ? 1 : -1;
        });
      });
    },
    //修改功能
    edit(item) {
      this.$refs.editProcess.$emit("fromParent", item);
    },
    //完成修改功能
    editFinish(item){
      this.items.forEach((i,index)=>{
          if (item.snkey == i.snkey){
            this.items.splice(index,1,item)
          }
        })
    },
    //刪除功能
    del(item) {
      //yn() 全域函數寫在main.js裡
      this.yn().then((result) => {
        /* Read more about isConfirmed, isDenied below */
        if (result.isConfirmed) {
          item.delman =
            this.$store.state.pData.name +
            "(" +
            dayjs().format("YYYY-MM-DD HH:mm:ss") +
            ")";

          var postData = {
            snkey: item.snkey,
            tablename: "materialimport",
            datalist: JSON.stringify(item),
          };

          let url =
            "general/delv3/" +
            this.$store.state.databaseName +
            "/materialimport";

          this.myAxios(url, postData).then((rs) => {
            if (rs.state == 1) {
              this.$swal({ icon: "success", text: "已刪除!" });
              this.getAllData();
            }
          });

          //更新庫存
          let matchData = this.$store.state.materialItems.find((i) => {
            return i.snkey == item.material.snkey;
          });
          matchData.store -= parseInt(item.quantity);
          this.updateStore("material", matchData); //自制更新庫存的全局函數('資料庫名',內容)
          //更新庫存end
        }
      });
    },
  },
};
</script>


<style lang="scss">
.MaterialImportList {
  .theme--light.v-data-table
    > .v-data-table__wrapper
    > table
    > thead
    > tr
    > th {
    font-size: 1rem;
  }
}
</style>